@import url('https://fonts.googleapis.com/css2?family=Orbitron&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');


p{
    margin: 0px;
}
body {
    max-width: 100vw;
    display: flex;
    justify-content: center;
    overflow-x: hidden;
}

.main-container {
    display: flex;
    flex-direction: column;
  
    width: 1354px;
    height: 1200px;
}

@media only screen and (max-width: 1400px){
    body {
        justify-content: flex-start;
        overflow-x: visible;
    }
}

#container {
    display: flex;
    align-items: center;
    justify-content: center;
    border: black solid 4px;
    height: 750px;
    width: 1350px;
    position: relative;
    top: 454px;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    background-color: lightgray;
}

#planet-box {
    display: flex;
    width: 33.333333333%;
    border-right: black solid 4px;
    justify-content: center;
    text-align: center;
    align-items: center;
    border-left: black solid 4px;
    border-bottom: black solid 4px;
}

#planet-name {
    display: flex;
    flex-direction: column;
    font-family: 'Orbitron';
    font-size: 30px;
    justify-content: space-around;
}

#instructions-modal-trigger{
    margin-top: 40px;
    font-family: "Ubuntu";
    font-size: 35px;
    font-weight: bolder;

}

#instructions-modal-trigger:hover {
    cursor: help;
}

#header-container {
    display: flex;
    position: relative;
    width: 1350px;
    height: 125px;
    z-index: 10;
    flex-direction: column;
}

#app-title {
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 50px;
    border-bottom: black solid 5px;
    height: 150px;
    font-family: "Ubuntu";
}

h3 {
    margin: 20px;
}

#orbital-period {
    display: flex;
    flex-direction: column;
    font-family: 'Orbitron';
    font-size: 30px;
}

#planet-temp {
    display: flex;
    flex-direction: column;
    font-family: 'Orbitron';
    font-size: 30px;
}

#personal-info-cont {
    display: flex;
    justify-content: space-between;
}

#creator > p {
    margin-top: 40px;
    font-family: "Ubuntu";
    font-size: 35px;
    font-weight: bolder;
}

.body-blackout {
    position: absolute;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .65);
    display: none;

    &.is-blacked-out {
        display: block;
    }
}

.popup-modal {
    height: 350px;
    width: 545px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 45px;
    opacity: 0;
    pointer-events: none;
    transition: all 300ms ease-in-out;
    z-index: 101;
    border-radius: 5%;

    &.is--visible {
        opacity: 1;
        pointer-events: auto;
    }

    &__close {
        position: absolute;
        font-size: 1.8rem;
        right: 10px;
        top: 7px;
        cursor: pointer;
    }
}

.modal-header-container {
    display: flex;
    justify-content: center;
    width: 100%;
    border-bottom: black solid 4px;
    margin-bottom: 50px;
}

.modal-header {
    font-family: "Ubuntu";
    font-size: 30px;
    font-weight: bolder;
}

.modal-body-content {
    font-family: "Ubuntu";
    font-size: 30px;

}

.fa-github {
    font-size: 40px;
    color: tomato;
}

.fa-linkedin-in {
    font-size: 40px;
    color: tomato;
}

.fa-angellist {
    font-size: 40px;
    color: tomato;
}

#link {
    display: flex;
    justify-content: space-around;
    width: 350px;
    padding-bottom: 36px;
    align-items: flex-end;
    margin-top: 40px;
}

.planet-details{
    width: 1358px;
    height: 115px;
    position: absolute;
    top: calc(50% + 453px);
    left: 50%;
    transform: translateX(calc(-100vw - 100%));
    background-color: lightgray;
    flex-direction: row;
    display: flex;
}

#planet-name-title {
    margin-bottom: 20px;
}

#orbital-period-title {
    margin-bottom: 20px;
}

#planet-temp-title {
    margin-bottom: 20px;
}

.planet-details[slide="in"] {
    animation: slideIn 1s ease-in forwards;
}

.planet-details[slide="out"] {
    animation: slideOut 1.5s ease-in forwards;
}

.planet-details[slide="next"] {
    animation: slideIn 1.5s ease-in forwards;
}

.canvas-details-container {
    position: relative;
    margin: 0px;
    top: 0%;
}

@keyframes slideNext {

    0% {
        transform: translateX(calc(-100vw - 100%));
    }

    50% {
        transform: translateX(calc(100vw + 100%));
    }

    51% {
        transform: translateX(calc(-100vw - 100%));
    }

    100% {
        transform: translateX(100%);
    }

}

@keyframes slideIn {
    from {
        transform: translateX(calc(-100vw - 100%));
    }

    to {
        transform: translateX(-50%);
    }
}

@keyframes slideOut {
    from {
        transform: translateX(-50%);
    }

    to {
        transform: translateX(100vw);
    }
}